@media (max-width: 768px) {
  form {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

select,
input[type="text"] {
  cursor: pointer;
  width: 150px;
  height: 50px;
  border-radius: 5px;
  display: inline-block;
  border: 1px solid #afafaf;
  color: #444;
  font-size: 18px;
  line-height: 50px;
  text-align: left;
  margin: 5px;
  padding: 0 5px;
}

select {
  width: 160px;
}

::placeholder {
  color: #7a7a7a;
}
